.main-card-container {
    display: grid;
    grid-template-columns: repeat(3, minmax(300px, 500px));
    grid-gap: 50px;
    justify-content: space-around;
}

.main-container {
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    align-items: flex-start;
    height: 580px;
    margin: 20px 30px;
    box-shadow: 10px 40px 18px grey;
}

.card-container {
    background-color: #fff;
    padding: 30px;
    position: relative;
    box-shadow: 0 0 5px rgba(75, 75, 75, 0.07);
    z-index: 1;
}

.body-container {
    display: inline-block;
}

.subtitle {
    display: block;
}

.card-number {
    margin: 15px;
    background-color: antiquewhite;
    text-align: center;
    width: 30px;
    height: 30px;
    border-radius: 31px;
    justify-items: center;
    font-size: 24px;
    color: brown;
}

.category{
    color: brown;
    text-transform: capitalize;
    margin-left: 17px;
    font-weight: 500;
}

.title{
    text-align: center;
    font-family: monospace;
    font-size: 29px;
    color: #48841F;
}

.description {
    text-align: justify;
    color: dimgray; 
    font-size: 18px;
}

.main-container img {
    width: 300px;
    height: 250px;
}

@media (max-width: 998px) {
    .main-card-container {
        display: grid;
        grid-template-columns: repeat(1, minmax(200px, 500px));
        grid-gap: 50px;
        justify-content: space-around;
    }
}